








































































































































































.topbar {
  width: 100vw;
  border: none;
  background-color: #f5f5f5;
}

.search-input {
  flex-grow: 1;
  height: 56px;
  font-size: 26px;
  color: #999;
  border: 1px solid #e6e6e6;
  border-radius: 10px;
  margin: 0 30px;
  background: #fff;
  line-height: 56px;
  text-align: center;
}

.icon-sousuo {
  display: inline-block;
  margin-right: 10px;
  font-size: 24px;
  color: #999;
}

.nav-wrapper {
  z-index: 10;
  width: 100vw;
  height: 80px;
  background: #fff;
}

::-webkit-scrollbar {
  width: 0;
  height: 0;
  color: transparent;
}
